/* MIXINS */
/* RESET */
html {
  font-size: 62.5%;
}

body {
  font-size: 1.3rem;
  background-color: #424242;
}

h1 {
  margin: 0;
}

@media (max-width: 650px) {
  h1 {
    font-size: 1.3em;
  }
}

a {
  text-decoration: none;
}

ul {
  padding: 0;
}

li {
  list-style: none;
}

img,
audio,
canvas,
iframe,
progress,
video {
  max-width: 100%;
  height: auto;
}

.article__content img, .article__content
audio, .article__content
canvas, .article__content
iframe, .article__content
progress, .article__content
video {
  display: block;
  margin: 0 auto;
}

img[src$=".svg"] {
  vertical-align: middle;
}

iframe {
  width: auto;
}

/* GENERAL STYLE */
body {
  display: flex;
  flex-wrap: wrap;
  width: 99vw;
}

@media (max-width: 650px) {
  body {
    display: block;
    width: 100vw;
  }
}

button {
  background-color: transparent;
  border: none;
}

.error {
  background-color: #c53d3d;
}

.wrapper {
  flex: 1 1 0;
  min-width: 0;
  min-height: 100vh;
  background-color: #FFF;
}

@media (max-width: 650px) {
  .wrapper {
    margin-bottom: 0px;
  }
}

.selected a {
  color: #FFF;
  font-weight: bold;
}

.notifications {
  margin: 0;
  color: #FFF;
  background-color: #424242;
}

.notifications__text {
  display: flex;
  padding: 1em 0.5em;
  color: #DDD;
  justify-content: space-between;
  align-items: center;
}

.folder__button, .mark-as-read, .article-favorite {
  border: 1px dashed transparent;
}

.folder__button:focus, .mark-as-read:focus, .article-favorite:focus {
  border-color: #DDD;
}

.sidebar-item-label, .folder__item a {
  display: block;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

/* FEED */
.event {
  position: relative;
  padding: 0 0.5em;
  border-bottom: 0.1em solid #D9D9D9;
}

.event:not(.event--focus):hover, .event--focused:not(.event--focus) {
  background-color: #DDD;
}

.event a {
  color: #424242;
}

.event--new-last {
  border-bottom-color: #424242;
  border-width: 4px;
}

/* SIDEBAR */
.sidebar {
  flex: 0 0 23.07692em;
  font-size: 0.9em;
  overflow: hidden;
}

.sidebar a {
  color: #fff;
}

.sidebar .settings__fields {
  padding: 20%;
}

[class*="__item"] {
  display: flex;
  position: relative;
  padding-bottom: 0.5em;
  padding-top: 0.5em;
  border-left: 0.38462em solid transparent;
}

[class*="__item"],
[class*="__item"] a {
  color: #fff;
}

[class*="__item"]:hover, [class*="__item"]:active {
  background-color: #303030;
}

[class*="__item"].selected {
  border-color: white;
}

.sidebar-item {
  flex-grow: 1;
  height: 16px;
  /* Favicon's height */
  line-height: 16px;
  overflow: hidden;
}

@media (max-width: 650px) {
  .folder__button,
  .sidebar-item {
    padding: 12px 0;
  }
}

.go-top {
  display: inline-block;
  padding: 2px 20px;
  text-align: center;
  background-color: #686868;
  border-radius: 20%;
}

.feed-add {
  text-align: center;
}

[data-is-opened="false"] {
  display: none;
}

.feed-add-title {
  margin: 0 0 1em;
  color: #fff;
}

.feed-add-url,
.feed-add-folders {
  width: 80%;
}

.feed-add-url {
  padding: 0.5em;
  border: none;
}

.feed-add-folders {
  margin: 1em 0;
  padding: 0.5em 2em;
}

.sidebar > section {
  margin: 2em auto 3em;
  padding: 0 1em;
}

.sidebar > section li {
  display: flex;
  color: #fff;
  justify-content: space-between;
}

.sidebar > section button,
.feed-add-button {
  padding: 1em 2em;
  color: #fff;
  background-color: #484848;
}

.sidebar > section button:hover, .sidebar > section button:active, .sidebar > section button:focus,
.feed-add-button:hover,
.feed-add-button:active,
.feed-add-button:focus {
  color: #484848;
  background-color: #DDD;
}

.feed-add-button {
  margin-bottom: 1em;
}

.feeds-list-toggle {
  position: relative;
  overflow: hidden;
}

.feeds-list-toggle:before {
  position: absolute;
  width: 15px;
  height: 15px;
  left: 35%;
  top: -20px;
  content: '';
  background-image: url("../images/close.svg");
  transition: transform 0.3s;
}

.is-opened.feeds-list-toggle:before {
  transform: translate3d(0, 180%, 0);
}

.feeds-list-toggle__label {
  position: absolute;
  padding: 0.5em 1.5em;
  top: 0;
  left: 0;
  transition: transform 0.3s;
}

.is-opened .feeds-list-toggle__label {
  transform: translate3d(0, 150%, 0);
}

#feeds-list[data-is-opened="false"] {
  display: block;
}

@media (max-width: 650px) {
  #feeds-list[data-is-opened="false"] {
    display: none;
  }
}

#feeds-list:target {
  display: block;
}

.folder {
  border-bottom: 0.2em solid #484848;
}

.folder__item a {
  margin: 0 0.3em;
}

.folder__button {
  float: left;
  margin-right: 5px;
  padding: 0;
  width: 10%;
}

.folder-closed img {
  transform: rotate(270deg);
}

.folder__item,
.feed__item {
  padding-left: 0.4rem;
  padding-right: 0.8rem;
}

.feed {
  padding: 4px 0;
  background-color: #4b4b4b;
}

html:not(.no-js) .feed--closed {
  display: none;
}

.feed__item img {
  float: left;
  margin-left: 1.4em;
  margin-right: 0.3em;
  vertical-align: middle;
}

@media (max-width: 650px) {
  .feed__item img {
    margin-right: 0.8em;
  }
}

.mark-as-read {
  padding: 0 0.4rem;
  font-size: 0.8em;
}

@media (max-width: 650px) {
  .mark-as-read {
    padding: 12px;
  }
}

/* ARTICLE */
.article__header {
  position: relative;
  padding: 0.5em 0;
  overflow: hidden;
  white-space: nowrap;
  cursor: pointer;
}

.event--focus .article__header {
  padding-bottom: 0.6em;
}

@media (max-width: 650px) {
  .article__header {
    display: grid;
    grid-template-columns: 40px auto 40px;
    grid-template-areas: "favicon site date" "favorite title mark";
    align-items: center;
    white-space: inherit;
    min-height: 4em;
  }
}

.article__header .article__feed-title {
  display: inline-block;
  width: 10%;
  color: #555;
  font-size: 0.9em;
  overflow-x: hidden;
  text-overflow: ellipsis;
  vertical-align: middle;
}

@media (max-width: 650px) {
  .article__header .article__feed-title {
    width: auto;
    grid-area: site;
  }
}

.feed-icon {
  margin-right: 0.3em;
  vertical-align: middle;
  justify-self: center;
  grid-area: favicon;
}

@media (max-width: 650px) {
  .feed-icon {
    margin: 0;
  }
}

@media (max-width: 650px) {
  .article__title {
    margin-left: 14%;
  }
}

@media (max-width: 650px) {
  .feed-icon ~ .article__title {
    margin-left: 0;
  }
}

.article__title {
  display: inline;
  font-size: 1em;
}

@media (max-width: 650px) {
  .article__title {
    display: inline-block;
    grid-area: title;
  }
}

[class~="event--read"] .article__title a {
  color: #BBB;
}

.article__description {
  display: inline-block;
  margin-left: 0.4em;
  color: #888;
}

[class~="event--read"] .article__description {
  color: #BBB;
}

@media (max-width: 650px) {
  .article__description {
    display: none;
  }
}

.event--focus .article__description {
  display: none;
}

.article__description:after {
  content: "";
  display: block;
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
  width: 16em;
  background-image: linear-gradient(to left, white 48%, rgba(255, 255, 255, 0) 100%);
}

.event:hover .article__description:after,
.event--focused .article__description:after {
  background-image: linear-gradient(to left, #DDD 6em, rgba(221, 221, 221, 0) 100%);
}

@media (max-width: 650px) {
  .event:hover .article__description:after,
  .event--focused .article__description:after {
    background: none;
  }
}

@media (max-width: 650px) {
  .article__description:after {
    background: none;
  }
}

.article__date {
  position: absolute;
  right: 5em;
  color: #424242;
  font-size: 0.9em;
}

@media (max-width: 650px) {
  .article__date {
    position: relative;
    right: 0;
    grid-area: date;
    justify-self: right;
  }
}

.article-favorite {
  position: absolute;
  padding: 0.6em;
  top: 0;
  right: 1.7em;
  opacity: 0.3;
}

.article-favorite--favorited {
  opacity: 1;
}

.article-favorite:focus {
  border-color: #424242;
}

@media (max-width: 650px) {
  .article-favorite {
    position: relative;
    padding: 0;
    width: 100%;
    right: 0;
    grid-area: favorite;
    justify-self: left;
  }
}

.article__read-unread {
  position: absolute;
  padding: 0.6em 0.3em;
  width: 1.7em;
  top: 0;
  right: 0;
  color: white;
  background-color: transparent;
  border: none;
  opacity: 0.3;
  cursor: pointer;
}

@media (max-width: 650px) {
  .article__read-unread {
    position: relative;
    padding: 0;
    width: 100%;
    height: 100%;
    grid-area: mark;
    justify-self: right;
  }
}

.article__read-read {
  opacity: 1;
}

.article__content {
  display: none;
  margin: 0 auto 1em;
  width: 42.8125em;
  font-size: 1.6rem;
  line-height: 1.7;
  overflow-wrap: break-word;
}

@media (max-width: 650px) {
  .article__content {
    padding: 0 1rem;
    width: auto;
  }
}

.article__content a {
  text-decoration: underline;
}

.event--website-view .article__content {
  width: auto;
}

.event--website-view .article__content iframe {
  width: 100%;
}

.article__content--is-opened {
  display: block;
}

.article__content__infos {
  margin: 1em 0;
}

.article__content__infos a {
  font-size: 2.8rem;
  line-height: 1;
}

.article__content__switch-view {
  display: none;
  position: absolute;
  right: 8em;
  top: 0.4em;
  background: none;
  border: 1px solid #BBB;
  z-index: 20;
}

@media (max-width: 650px) {
  .article__content__switch-view {
    top: 2.4em;
    right: 4em;
  }
}

.event--focus .article__content__switch-view {
  display: inherit;
}

/* USER SETTINGS */
.user-login,
.settings {
  display: flex;
  justify-content: space-around;
  margin: 1em auto;
  padding: 0 1em;
  color: #CACACA;
  align-items: center;
  text-align: center;
}

.user-login {
  flex-direction: column;
}

.settings-item {
  padding: 0.5em 1.5em;
  min-width: 15px;
  min-height: 15px;
  border-bottom: 0.38462em solid transparent;
  box-sizing: content-box;
  transition: border 0.2s ease-in-out 0s;
}

.settings-item:hover, .settings-item:active, .settings-item:focus {
  border-color: white;
}

button.settings-item {
  border-top-width: 0;
  border-right-width: 0;
  border-left-width: 0;
  background-color: transparent;
}

.settings__fields input[type='text'],
.settings__fields input[type='password'] {
  min-width: 7em;
  width: 35%;
}

.settings__fields input[type='text'],
.settings__fields input[type='password'],
.settings__fields input[type='submit'] {
  display: block;
  margin: 0.4em auto;
  padding: 0.5em;
  color: #303030;
}

.favorite-inverted {
  fill: white;
}

.animation-spin {
  animation: spin 2s linear infinite;
}

@keyframes spin {
  100% {
    transform: rotate(360deg);
  }
}

.shortcuts-toggle {
  display: none;
}

@media (min-width: 650px) {
  html:not(.no-js) .shortcuts-toggle {
    display: block;
  }
}

.shortcuts {
  position: fixed;
  display: none;
  left: 0;
  top: 0;
  padding: 20px;
  width: 100%;
  height: 100%;
  color: white;
  background-color: #303030e6;
  overflow: scroll;
}

.shortcuts:hover {
  cursor: pointer;
}

.shortcuts-list {
  margin: 0;
  padding: 0;
}

.shortcut {
  display: flex;
  margin-bottom: 10px;
  align-items: center;
}

.shortcut-key {
  display: inline-block;
  margin-right: 20px;
  padding: 5px 5px 10px 5px;
  min-width: 20px;
  color: #777777;
  font-size: 12px;
  background-color: #DDDDDD;
  border: 2px solid #F9F7F7;
  border-bottom: 4px solid #898989;
  border-right: 4px solid #898989;
  border-radius: 3px;
  box-shadow: 0 0 10px #111010;
}

.shortcut-key + .shortcut-key {
  margin-left: 20px;
}

/* MEDIA QUERIES */
@media (max-width: 650px) {
  td {
    display: block;
  }
}

.mobile-only {
  display: none;
}

@media (max-width: 650px) {
  .mobile-only {
    display: inline;
  }
}

/* IE9- */
.no-flexboxlegacy body {
  display: block;
}

.no-flexboxlegacy .wrapper {
  margin-left: 20%;
}

.no-flexboxlegacy .sidebar {
  float: left;
  width: 20%;
}

/* IE7 */
.no-flexboxlegacy.no-boxsizing .sidebar {
  width: 19%;
}

.hidden {
  display: none;
}
